import React from 'react';
import { CloseOutlined } from '@ant-design/icons';
import { narrativespec2Vis } from '@/tool/narrative2cards';
import { fact2unitVis } from '@/tool/fact2unitVis';

import uuidv4 from 'uuid/v4';
import ActionType from '@/action/type';
import UnitVisDraw from './UnitVisDraw';
import ChartType from '../../../../constant/ChartType';
import SliceCardFooter from './SliceCardFooter';
import SliceCardCaption from './SliceCardCaption'
import * as api from '@/axios/api';
import OperationType from '@/constant/OperationType';
import config from '@/axios/config';
import Fact from '@/model/fact';
import { genFactSentence } from '@/sentencer/index';
import { isValid, customizeFact } from '@/view/FactEditor/helper';
import getSupportedChartTypes from '@/tool/getSupportedChartTypes';
import _ from 'lodash';


export const getChoice = (length) => {
    let choice = Math.round(Math.random() * (length - 1));
    return choice;
}

export const getFactChartType = (shema, fact, choice = 0) => {
    let supportedChartTypes = getSupportedChartTypes(fact, shema);
    if (supportedChartTypes.length === 0) return null;
    return supportedChartTypes[getChoice(supportedChartTypes.length)].chart;
}

export default class SliceCard extends React.Component {


    deleteVideoSlice = (index) => {
        if(this.props.isPlayVideo)
        {
            null
        }
        else
        {
        this.props.removeCard(index)
        this.props.deleteBarFromTimeLineBar(index)
        this.props.deleteFact(index)
        }
    }

    generateTransitionFacts = (index) => {
        new Promise(async (resolve, reject) => {
            console.log("generateTransitionFacts"+index)
            let formerFact = this.props.cardItems[index]
            let latterFact = this.props.cardItems[index+1]

            let filename = this.props.fileName
            // 5start  end fact
            let startFact = {
                type:formerFact.fact.type,
                measure:formerFact.fact.measure,
                subspace:formerFact.fact.subspace,
                groupby:formerFact.fact.groupby,
                focus:formerFact.fact.focus,
            }
            let endFact = {
                type:latterFact.fact.type,
                measure:latterFact.fact.measure,
                subspace:latterFact.fact.subspace,
                groupby:latterFact.fact.groupby,
                focus:latterFact.fact.focus,
            }
            
            this.setState({
                operateState: OperationType.GENERATING
            });
            this.props.isUpdateEditing(true);

            let data = {
                file_name:filename,
                max_story_length:4,
                method:"guided",
                start:startFact,
                end:endFact,
            }
            // api
            const response = await api.generate(config.url.generateRandomly,data)

            if (response.data.fail) {
                console.log('fail')
                this.setState({
                    operateState: OperationType.ADD_FAILED,
                    errorMessage: response.data.fail
                })
                reject()
                //record_activity(activitiesCode.editTopic, 0, { filename: fileName, filters: filters });
            }
            if (response.data.error) {
                console.log('error')
                this.setState({
                    operateState: OperationType.ADD_FAILED,
                    errorMessage: response.data.error
                })
                reject()
                //record_activity(activitiesCode.editTopic, 0, { filename: fileName, filters: filters });
            }
            const facts = response.data.story.facts;
            let tempFacts = [];
            for (let factDict of facts){    
                let fact = new Fact(
                    factDict['type'],
                    factDict['measure'],
                    factDict['subspace'],
                    factDict['groupby'],
                    factDict['focus'],
                    factDict['parameter'],
                    "", // chart
                    factDict['score'],
                    factDict['information'],
                    factDict['significance']
                )
                fact.generatedScript = genFactSentence(fact, this.state.lang)
                tempFacts.push(fact);
            }
            console.log(tempFacts.length)

            var i = 0
            var myCards = []
            var myBars =[]

            for(let fact of tempFacts){
                if(tempFacts.length==1||tempFacts.length==2)
                {
                    break
                }
                if(i==0||i==tempFacts.length-1)
                {
                    i++
                    continue
                }
                
                let schemaFilter = _.cloneDeep(this.props.schemaFilterLocal);
                let schema=this.props.schema
                let specData
                schemaFilter ? specData = { "schema": schemaFilter, } : specData = { "schema": schema };

                if (!fact.chart || fact.chart === "") {
                    fact.chart = getFactChartType(specData.schema, fact); //chart
                }
            
                let chart = fact.chart;
            
                if (chart === ChartType.ISOTYPE_BAR_CHART) {
                    fact.chart = ChartType.VERTICAL_BAR_CHART;
                }
                // Fact {}
                // fact = customizeFact(fact);
                if (!fact.chart || !isValid(fact)) {
                    console.log("no valid", fact)
                    return null;
                }

                let datares = _.cloneDeep(this.props.dataresLocal);

                specData = {
                    ...specData,
                    values: datares,
                }

                let width_height = { "width": 135, "height": 86 };
                let size = null;
                let specChart;
                if (width_height) {
                    specChart = {
                        width: width_height.width,
                        height: width_height.height,
                        type: "unitvis",
                        style: "business",
                        duration: 0,
                        showSuggestion: true, //Unsupported
                    }
                } else {
                    specChart = {
                        size: size,
                        type: "unitvis",
                        style: "business",
                        duration: 0,
                        showSuggestion: true, //Unsupported
                    }
                }
                let specFact = {
                    ...fact,
                    breakdown: fact.groupby.map(d => {
                        return {
                            "field": d
                        }
                    }),
                    index:i
                }

                let specNew = {
                    data: specData,
                    fact: specFact,
                    chart: specChart,
                }
                let specs = specNew
                let time = 6000;
                let insertIndex = index + i
                //facts
                this.props.insertFactByFact(fact,insertIndex)
                // keyframes
                //this.props.addCard(fact,specs,time,true, insertIndex);
                let myCard = {
                    fact:fact,
                    spec:specs,
                    time:time,
                    isGeneratedByAlg:true}
                myCards.push(myCard)
                // timebar
                let bar = { leftbar: 140, rightbar: 18,isDrag:false }
                myBars.push(bar)
                //this.props.addBarToTimeLineBar(bar,insertIndex)
                i++;
            }
            
            // units
            this.props.addCards(myCards,index+1)
            this.props.addBarsToTimeLineBar(myBars,index+1)
            
            this.setState({
                operateState: OperationType.GENERATED
            })
            this.props.isUpdateEditing(false)
            resolve(response)


        })
    }



    render() {
    
        const { videoIndex, index } = this.props
        let datares = _.cloneDeep(this.props.dataresLocal);
        let schemaFilter = _.cloneDeep(this.props.schemaFilterLocal);
        let schema = this.props.schema
        let specData
        schemaFilter ? specData = { "schema": schemaFilter, } : specData = { "schema": schema };
        let uuid = uuidv4();
        let vis;

        if (this.props.spec !== 'empty') {
            vis = narrativespec2Vis(this.props.spec, uuid);
        } else {
            vis = null;
        }
        console.log('herererre')
        return (
            <div
                className='video-slice-card'
                ref={this.props.provided.innerRef}
                {...this.props.provided.draggableProps}
                {...this.props.provided.dragHandleProps}>
                <div
                    className='complete-card'
                    // style={videoIndex === index ? this.props.cardItems[index].isGeneratedByAlg?{border:"3px solid #E4F2FB",boxShadow: "0 0 0 3px #ffb861, 0 0 0 2px #FFE1A4"}:{border:"3px solid #FFE1A4",boxShadow: "0 0 0 3px #ffb861, 0 0 0 2px #FFE1A4"}: this.props.cardItems[index].isGeneratedByAlg?{border:"3px solid #E4F2FB"}:{border:"3px solid #FFE1A4"}}>
                    // style={{border:"3px solid #E4F2FB"}}>         
                    style={{background: '#f4f4f4'}}>           
                    <div className='above-card'>
                        <div className='fact-description'></div>
                        <div className='delete-card-btn' onClick={() => this.deleteVideoSlice(index)}>
                            <CloseOutlined />
                        </div>
                    </div> 
                    

                    <UnitVisDraw
                        index={index}
                        videoIndex={videoIndex}
                        vis={vis}
                        playVideoSlice={this.props.playVideoSlice}
                        setLeftsiderContent={this.props.setLeftsiderContent}
                        {...this.props}
                    />

                    <SliceCardCaption
                        spec = {this.props.spec}
                        videoIndex={videoIndex} 
                        index={index}
                        {...this.props}/>

                    {/* <SliceCardFooter
                        videoIndex={videoIndex} 
                        index={index}
                        {...this.props}/> */}
                </div>
                {/* {
                this.props.cardItems.length-1 == index ?
                <div className="transition-button" style={{opacity:'0'}}>
                <div className="transition-icon"></div>
                </div>
                :
                ((this.props.cardItems[index].isGeneratedByAlg||this.props.cardItems[index+1].isGeneratedByAlg)?
                <div className="transition-button">
                    <div className="cannot-transition-icon"></div>
                </div>
                :
                <div className="transition-button" onClick={() => this.generateTransitionFacts(index)}>
                    <div className="transition-icon"></div>
                </div>
                )} */}

                {/* <div className="transition-button" style={{opacity:'0'}}>
                <div className="transition-icon"></div>
                </div> */}
            </div>
        )
    }
}