import React from 'react';
import { is } from 'immutable'
import Immutable from 'immutable'

export default class SliceCardFooter extends React.Component {
    
    render() {
        const {index,videoIndex}=this.props
        let num=index + 1 < 10 ? ("0" + (index + 1)) : ("" + (index + 1))
        return (
            <div className='footer-card'>
                <div className='footer-num' style={{backgroundColor:"#E4F2FB"}}>
                    {num}
                </div>
            </div>
        )
    }

}